/**
 * @packageDocumentation
 * @module Serializer
 */
import { sortPlugins } from '@ali/4ever-utils';
import { MoConfig } from './type';
import type {
  MoSerializationPluginFactory as PluginFactory,
  MoSerializationPlugin as Plugin,
} from '@ali/4ever-cangjie';

/**
 * 创建自定义插件。传入自定义的插件 Map，返回一个插件创建函数（createMoPlugins）。
 * 传入内容格式：{ PluginName: PluginFactory }
 */
function createCustomizedMoPlugins(plugins: Record<string, PluginFactory>) {
  return function createMoPlugins(configs: MoConfig = {}): Plugin[] {
    const result: Plugin[] = [];
    sortPlugins<Plugin>(plugins).forEach(({ factory }): void => {
      const plugin = factory(configs);
      result.push(plugin);
    });
    return result;
  };
}

export default createCustomizedMoPlugins;
